/*
 * Component Chancemeter
 */


(function (window, $, nn) {

  nn.component.ChanceMeter = function(element) {
    this.element = $(element);
    this.init();
  };

  // Additional chancemeter functionality
  nn.component.ChanceMeter.prototype = {

    init: function () {
      // not needed in responsive?
      this.handle = this.element.find('[data-role="handle"]');
      this.tooltip = this.element.find('[data-role="tooltip"]');
      this.element.on('update', $.proxy(this.updateHandler, this));
      var initialValue = parseInt(this.element.find('[data-role="initial-value"]').val(), 10);
      this.setValue(initialValue);
    },
    update: function (updateProperties) {

      if (updateProperties.showelement && updateProperties.showelement === "false") {
        this.hide();
      }
      this.show();
      this.setValue(updateProperties.value);
    },
    setValue: function (value) {
      var percentage = this.getPercentage(value);
      this.handle.stop(true, false).animate({bottom: percentage}, 1000);
      if (!this.element.hasClass('chancemeter--no-value')) {
        this.tooltip.html(percentage);
      } else {
        this.tooltip.html('');
      }
    },
    getPercentage: function (value) {
      var v = parseInt(value, 10);
      v = Math.abs(v);
      v = Math.min(v, 100);
      return v+'%';
    },
    updateHandler: function (event, eventProperties) {
      this.update(eventProperties);
    },
    show: function () {
      if (this.isVisible) {
        return;
      }
      this.element.show();
    },
    hide: function () {
      if (!this.isVisible) {
        return;
      }
      this.element.hide();
    }
  };

})(window, jQuery, nn);