import moduleconfig from '../u-moduleconfig/moduleconfig';
window.app = window.app || {};
window.app.vaZoeker = window.app.vaZoeker || {};

window.app.vaZoeker.Index = async function (element) {
  const {default: VaZoeker} = await import(/* webpackChunkName: "va-zoeker" */ './vaZoeker');
  const {apiKey} = moduleconfig({element});
  return new VaZoeker(element, {apiKey})
    .init();
};

window.app.vaZoeker.redirector = async function (element) {
  const {default: VaRedirector} = await import(/* webpackChunkName: "va-zoeker-redirector" */ './vaRedirector');
  const { redirectUrl, aanstelling, typeKlant } = moduleconfig({element});
  return new VaRedirector(element, { redirectUrl, aanstelling, typeKlant })
    .init();
};
