window.nn.registerModule('corporateAccountManagement', async function (root, config) {
  ['showCreateAccount', 'showDelegateAccount', 'showManageAccount'].forEach(k => {
    if (!config[k]) {
      config[k] = true;
    } else {
      config[k] = (config[k] === 'true') ? true : false
    }
  });

  const bootstrapAccountManagementPromise = import(/* webpackChunkName: "corporate-accounts" */ './accountManagement');
  const bootstrapAccountManagement = (await bootstrapAccountManagementPromise).default;

  bootstrapAccountManagement(root, config);
});
