import moment from 'moment';
import indexOf from 'lodash/indexOf';

(function (window, $, nn) {
  var textualMonthsShort = ['jan', 'feb', 'mrt', 'apr', 'mei', 'jun', 'jul', 'aug', 'sep', 'okt', 'nov', 'dec'];
  var textualMonthsLong = ['januari', 'februari', 'maart', 'april', 'mei', 'juni', 'juli', 'augustus', 'september', 'oktober', 'november', 'december'];

  function convertLegacyDate(str) {
    if (str.match(/^\d{4}-\d{1,2}-\d{1,2}$/)) {
      str = str.split('-').reverse().join('-');
    }

    return str;
  }

  function parseDateString(str) {
    var matches,
      date,
      year,
      month,
      day;

    // Try format DD-MM-YYYY, separator can be a single space or any of these: -/.
    if (!date) {
      matches = str.match(/(\d{1,2})[ /.-](\d{1,2})[ /.-](\d{4})/);
      if (matches && matches.length > 3) {
        year = parseInt(matches[3], 10);
        month = parseInt(matches[2], 10) - 1;
        day = parseInt(matches[1], 10);

        date = new Date(year, month, day);
        date.setFullYear(year);
      }
    }

    // Try format YYYY-MM-DD, separator can be a single space or any of these: -/.
    if (!date) {
      matches = str.match(/(\d{4})[ /.-](\d{1,2})[ /.-](\d{1,2})/);
      if (matches && matches.length > 3) {
        year = parseInt(matches[1], 10);
        month = parseInt(matches[2], 10) - 1;
        day = parseInt(matches[3], 10);

        date = new Date(year, month, day);
        date.setFullYear(year);
      }
    }

    // Try format DD MMM YYYY or DD MMMM YYYY, separator is a single space
    if (!date) {
      matches = str.match(/(\d{1,2})[ /.-]([a-z]+)[ /.-](\d{4})/);
      if (matches && matches.length > 3) {
        year = parseInt(matches[3], 10);
        month = matches[2].length === 3 ? indexOf(textualMonthsShort, matches[2]) : indexOf(textualMonthsLong, matches[2]);
        day = parseInt(matches[1], 10);

        if (month > -1 && month < 12) { // Month int has to be 0-11 for the Date() object
          date = new Date(year, month, day);
          date.setFullYear(year);
        }
      }
    }

    return date || null;
  }

  function formatDateString(date) {
    return [
      date.getDate(),
      date.getMonth() + 1,
      date.getFullYear()
    ].join('-');
  }

  nn.component.DatepickerField = function (element) {
    var $element = $(element),
      $input = $('#' + $element.prop('id').replace('-dummy', '')),
      data = $element.data(),
      value = $input.val(),
      config,
      today = new Date();

    today.setHours(0, 0, 0, 0);

    config = {
      dateStart: new Date(today.getFullYear() - 150, today.getMonth(), today.getDate()),
      dateEnd: new Date(today.getFullYear() + 150, today.getMonth(), today.getDate())
    };

    if (value) {
      $input.val(convertLegacyDate(value));
    }

    if (data.startDate && data.startDate.indexOf('-') > -1) {
      config.dateStart = parseDateString(data.startDate);
    }

    if (data.endDate) {
      config.dateEnd = parseDateString(data.endDate);

      if (!data.startDate) {
        config.dateStart = new Date(today.getFullYear() - 100, today.getMonth(), today.getDate());
        config.dateFocus = new Date(today.getFullYear() - 30, today.getMonth(), today.getDate());
      }
    }

    if (data.focusDate) {
      config.dateFocus = parseDateString(data.focusDate);
    }

    ////

    if (data.endDate && config.dateFocus && (config.dateFocus > config.dateEnd)) {
      config.dateFocus = config.dateEnd;
    }

    if (data.startDate && config.dateFocus && (config.dateFocus < config.dateStart)) {
      config.dateFocus = config.dateStart;
    }

    // Format into D-M-YYYY
    if (config.dateFocus) {
      config.dateFocus = formatDateString(config.dateFocus);
    }
    if (config.dateStart) {
      config.dateStart = formatDateString(config.dateStart);
    }
    if (config.dateEnd) {
      config.dateEnd = formatDateString(config.dateEnd);
    }

    if (data.disableWeekends) {
      config.disabledWeekDays = [6, 0];
    } else {
      if (data.disableSaturdays) {
        config.disabledWeekDays = config.disabledWeekDays || [];
        config.disabledWeekDays.push(6);
      }
      if (data.disableSundays) {
        config.disabledWeekDays = config.disabledWeekDays || [];
        config.disabledWeekDays.push(0);
      }
    }

    // window.console.info('DatepickerLegacy config for '+$element.prop('id'),config, element, $input[0]);

    if (data.datePickerToggle) {
      config.preventTextInput = true;
    }

    return new window.app.form.datepicker($element, config);
  }

  nn.component.DatepickerLegacy = function (element) {
    this.element = $(element);
    this.init();
  };

  nn.component.DatepickerLegacy.prototype = {
    init: function () {
      this.getAndSetOptions();
      this.getAndSetElements();
      this.bindEvents();
    },
    getAndSetOptions: function () {

      // Constants
      this.FORMAT_SYS = 'YYYY-MM-DD';
      this.FORMAT_DUTCH = 'DD-MM-YYYY';
      this.FORMAT_DUTCH_READABLE = 'D MMM YYYY';

      // defaults
      this.pickerOptions = {
        now: moment().format(this.FORMAT_SYS),
        dateStart: moment().subtract(100, 'years').format(this.FORMAT_SYS),
        dateEnd: moment().add(100, 'years').format(this.FORMAT_SYS),
        dateFocus: moment().format(this.FORMAT_SYS),
        dateSelected: null
      };

      this.ignoreNextChange = false; // since we will be triggering change in here, we need to skip some triggers

      this.picker = new nn.component.Datepicker();
      this.picker.setField(this);

      this.startDateMoment = this.convertToMoment(this.element.attr('data-start-date'));
      this.endDateMoment = this.convertToMoment(this.element.attr('data-end-date'));
      this.focusMoment = this.convertToMoment(this.element.attr('data-focus-date'));
      this.now = moment();

      if (moment.isMoment(this.startDateMoment) && this.startDateMoment.isValid()) {
        this.pickerOptions.dateStart = this.startDateMoment.format(this.FORMAT_SYS);
      }

      if (moment.isMoment(this.endDateMoment) && this.endDateMoment.isValid()) {
        this.pickerOptions.dateEnd = this.endDateMoment.format(this.FORMAT_SYS);

        if (!moment.isMoment(this.startDateMoment) || !this.startDateMoment.isValid()) {
          this.startDateMoment = moment().subtract(100, 'years');
          this.pickerOptions.dateStart = this.startDateMoment.format(this.FORMAT_SYS);
          this.pickerOptions.dateFocus = moment().subtract(30, 'years').format(this.FORMAT_SYS);
        }
      }

      if (moment.isMoment(this.focusMoment) && this.focusMoment.isValid()) {
        this.pickerOptions.dateFocus = this.focusMoment.format(this.FORMAT_SYS);
      }

      var focusCheck = this.convertToMoment(this.pickerOptions.dateFocus);
      if (moment.isMoment(focusCheck) && focusCheck.isBefore(this.startDateMoment)) {
        this.pickerOptions.dateFocus = this.now.format(this.FORMAT_SYS);
      }

      var disabledWeekDays = [];
      // Store an array with disabled weekdays.
      // Weekdays are zero-indexed, so values are 0-6
      if (this.element.attr('data-disable-weekends')) {
        disabledWeekDays.push(5, 6);
      }

      if (this.element.attr('data-disable-saturdays')) {
        disabledWeekDays.push(5);
      }

      if (this.element.attr('data-disable-sundays')) {
        disabledWeekDays.push(6);
      }

      if (disabledWeekDays.length > 0) {
        this.pickerOptions.disabledWeekDays = disabledWeekDays;
      }

      if (this.element.attr('readonly')) {
        this.isReadOnly = true;
      } else {
        this.isReadOnly = false;
      }

      if (this.element.attr('data-date-picker-toggle')) {
        this.preventTextInput = true;
        this.element.addClass('like-not-readonly').attr('readonly', true);
      } else {
        this.preventTextInput = false;
      }
    },
    getAndSetElements: function () {

      this.inputHidden = $('#' + this.element.attr('id').replace('-dummy', ''));

      var currentMoment = this.convertToMoment(this.inputHidden.val());
      if (moment.isMoment(currentMoment)) {

        this.selectedMoment = currentMoment;
        this.pickerOptions.dateSelected = this.selectedMoment.format(this.FORMAT_SYS);
        this.pickerOptions.dateFocus = this.selectedMoment.format(this.FORMAT_SYS);
        //				this.focus = this.pickerOptions.dateFocus = currentMoment.format('YYYY-MM-DD');
        this.element.val(currentMoment.format(this.FORMAT_DUTCH_READABLE));
        //				this.pickerOptions.now = currentMoment.format(this.FORMAT_SYS);
      }

      this.wrapperElement = this.element.wrap('<div class="datepickerfield-wrapper" />').parent('.datepickerfield-wrapper');
      this.datepickerIcon = $('<a href="#" class="datepickerfield-icon"></a>').appendTo(this.wrapperElement);
    },
    bindEvents: function () {
      this.datepickerIcon.on('click', $.proxy(this.onIconClick, this));
      this.picker.element.on('Datepicker:Datepicked', $.proxy(this.onDateSet, this));
      if (this.preventTextInput) {
        this.element.on('click', $.proxy(this.openPicker, this));
        var self = this;
        this.element.keydown(function (e) {
          if (e.keyCode !== 9) {
            self.openPicker();
            e.preventDefault();
          }
        });
      }
      this.element.on('change', $.proxy(this.onChange, this));
      $(window).on('resize', $.proxy(this.onResizeHandler, this));
    },
    onIconClick: function (evt) {
      evt.preventDefault();

      if (!this.isReadOnly) {
        this.openPicker();
      }
    },
    onResizeHandler: function () {
      this.picker.repositionDialog(this.datepickerIcon);
    },
    onChange: function () {
      if (this.ignoreNextChange === true) {
        this.ignoreNextChange = false;
      } else if (this.element.val() === "") {
        this.setDateToField("");
      } else {
        var date = this.convertToMoment(this.element.val());
        if (moment.isMoment(date)) {
          if (moment.isMoment(this.startDateMoment) && date.isBefore(this.startDateMoment)) {
            date = this.startDateMoment;
          }

          if (moment.isMoment(this.endDateMoment) && date.isAfter(this.endDateMoment)) {
            date = this.endDateMoment;
          }
          this.pickerOptions.dateFocus = date.format(this.FORMAT_SYS);
          this.setDateToField(date);
        } else {
          this.setDateToField("");
        }
      }
    },

    // hiddenfield update niet als er iets geks in veld staat. why?

    onDateSet: function (evt, data) {
      // needed for IAF to detect change;
      this.element.trigger('change');

      this.setDateToField(data.moment);
    },
    setDateToField: function (date) {
      if (moment.isMoment(date)) {
        this.pickerOptions.dateFocus = this.pickerOptions.dateSelected = date.format(this.FORMAT_SYS);
        this.inputHidden.val(date.format(this.FORMAT_DUTCH));
        this.ignoreNextChange = true;
        this.element.val(date.format(this.FORMAT_DUTCH_READABLE)).change();
      } else {
        this.inputHidden.val(this.element.val());
      }
      this.element.focus();
    },
    openPicker: function () {
      this.picker.setOptions(this.pickerOptions);
      this.picker.show(this.datepickerIcon);
    },
    convertToMoment: function (dateString) {
      return nn.helpers.dateToMoment(dateString);
    }
  };

})(window, jQuery, nn);
