import nn from '@nn/psdl-legacy';
import sscConfig from './sscConfig.js';

// Set the ssc config in nn.globals.SSC for compatibility
// It is unknown where this may be used outside of known code in this project
nn.globals.SSC = sscConfig;

const loadAndCreateApp = async function (modulePromise, element, config) {
  const module = (await modulePromise).default;
  const app = module(element, config);

  return Promise.resolve(app);
};

nn.registerModule('faq.CategoryList', async function (element, config) {

  config = Object.assign({}, sscConfig, config);
  const modulePromise = import(/* webpackChunkName: "modules" */ "./faqCategoryList.js");
  return loadAndCreateApp(modulePromise, element, config);
});

nn.registerModule('faq.QuestionList', async function (element, config) {
  config = Object.assign({}, sscConfig, config);

  // LEGACY compatibility
  if (config.style === 'collapse') {
    const modulePromise = import(/* webpackChunkName: "modules" */ "./legacy/ssc.Questionlist.js");
    // This is a legacy compatibility app to render faq-questions separate from menu
    // The data and legacy mode is shared through the store
    return loadAndCreateApp(modulePromise, element, config);
  }
  // end of LEGACY

  const modulePromise = import(/* webpackChunkName: "modules" */ "./faqQuestionList.js");
  return loadAndCreateApp(modulePromise, element, config);
});

// LEGACY compatibility
nn.component.SSCFaqMenu = function (element) {
  const options = {
    "classificationId": element.getAttribute('data-classification-id'),
    "categoryId": element.getAttribute('data-category-id'),
    "maxLevelDepth": element.getAttribute('data-levels'),
    "columns": element.getAttribute('data-wrap-count'),
    "detailUrl": element.getAttribute('data-detail-url')
  };
  element.setAttribute('data-config', JSON.stringify(options));
  return window.app.faq.CategoryList(element);
};
nn.registerModule('faq.sscQuestionList', async function (element) {
  const modulePromise = import(/* webpackChunkName: "modules" */ "./legacy/ssc.Questionlist.js");
  return loadAndCreateApp(modulePromise, element);
});
window.app.ssc = {
  Categorylist: window.app.faq.CategoryList,
  Questionlist: window.app.faq.QuestionList
};
// end of LEGACY
