function loadStyleSheet(url) {
  return new Promise((resolve, reject) => {

    let style = document.querySelector(`link[href="${url}"]`);

    if (!style) {
      style = document.createElement('link');
      style.type = "text/css";
      style.rel = "stylesheet";
      style.dataset.loadstate = 'loading';

      style.addEventListener('load', () => {
        style.dataset.loadstate = 'loaded';
        resolve(true);
      });

      style.addEventListener('error', error => {
        style.dataset.loadstate = 'error';
        style.dataset.errormsg = error;
        reject(error);
      });

      style.href = url;
      document.head.appendChild(style);
    }

    if (style.dataset.loadstate === undefined || style.dataset.loadstate === 'loaded') {
      resolve(true);
    } else if (style.dataset.loadstate === 'error') {
      reject(style.dataset.errormsg);
    }

  });
}

export default loadStyleSheet;