import { parseQueryString, queryString } from "@nn/psdl-utility/src/query-string";
import * as location from '@nn/psdl-utility/src/location';

window.nn.registerModule('bluejs', async (element, config) => {
  const { bluejs } = await import(/* webpackChunkName: "bluejs" */ './wrappers');

  bluejs(element, {
    contextRoot: config.contextRoot,
    startupParams: buildStartupParams(config),
    components: window.bluejsComponents,
  });
});

window.nn.registerBluejsComponents = (components) => {
  window.bluejsComponents = window.bluejsComponents || {};

  Object.keys(components).forEach(key => {
    window.bluejsComponents[key] = components[key];
  });
};

const buildStartupParams = (config) => {
  const startupParams = {};

  if (config.project && config.flow) {
    startupParams.project = config.project;
    startupParams.flow = config.flow;
  }
  if (config.shortcut) {
    startupParams.shortcut = config.shortcut;
  }
  if (config.channel) {
    startupParams.channel = config.channel;
  }
  if (config.version) {
    startupParams.version = config.version;
  }
  const ticketId = tryGetTicketIdfromAquimaParameters(location.search());
  if (ticketId) {
    startupParams.additional = { ticketId };
  }
  return startupParams;
};

const tryGetTicketIdfromAquimaParameters = (input) => {
  parseQueryString(input);
  let params = decodeURIComponent(queryString('initialValues') || '').toLowerCase();
  params = params.substring(params.indexOf('<ticketid>') + 10);
  params = params.substring(0, params.indexOf('</ticketid>'));
  return params;
};
