nn.component.tableInteractions = function (element) {
  this.element = $(element);
  if ($('html').hasClass('no-touchevents')) {
    this.init();
  }
};

$.extend(nn.component.tableInteractions.prototype, {
  init: function () {

    this.getAndSetOptions();
    this.getAndSetElements();
    this.bindEvents();

  },
  getAndSetOptions: function () {


  },
  getAndSetElements: function () {
    var _this = this;
    this.controls = this.element.data("controls");
    this.element.find('.data-table-row').each(function () {
      $(this).find('.' + _this.controls).stop().animate({'opacity': '0.5'}, 100);
    });
  },
  bindEvents: function () {
    var _this = this;
    var controls = _this.controls;

    this.element.find('.data-table-row').each(function () {
      $(this).on('mouseenter', function () {
        $(this).find('.' + controls).stop().animate({'opacity': '1'}, 100);
      });
      $(this).on('mouseleave', function () {
        $(this).find('.' + controls).stop().animate({'opacity': '0.5'}, 100);
      });
    });
  }
});