nn.registerModule('gdprOverview', async (element) => {
  const gdprOverviewTplPromise = import(/* webpackChunkName: "gdpr" */ './gdprOverview.tpl.hbs');
  const gdprOverviewDialogTplPromise = import(/* webpackChunkName: "gdpr" */ './gdprOverviewDialog.tpl.hbs');
  const gdprOverviewDialogDoneTplPromise = import(/* webpackChunkName: "gdpr" */ './gdprOverviewDialogDone.tpl.hbs');
  const gdprOverviewPromise = import(/* webpackChunkName: "gdpr" */ './gdprOverview.js');

  const gdprBlueconicSourcePromise = import(/* webpackChunkName: "gdpr" */ './gdprBlueconicSource.js');
  const gdprGetBusinessPartnerDataSourcePromise = import(/* webpackChunkName: "gdpr" */ './gdprGetBusinessPartnerDataSource.js');
  const gdprGetPartyAgreementsSourcePromise = import(/* webpackChunkName: "gdpr" */ './gdprGetPartyAgreementsSource.js');
  const gdprGetActivityForCustomerSourcePromise = import(/* webpackChunkName: "gdpr" */ './gdprGetActivityForCustomerSource.js');
  const gdprGetMarketingDataSourcePromise = import(/* webpackChunkName: "gdpr" */ './gdprGetMarketingDataSource.js');

  const templates = {
    main: (await gdprOverviewTplPromise).default,
    dialog: (await gdprOverviewDialogTplPromise).default,
    dialogDone: (await gdprOverviewDialogDoneTplPromise).default,
  };

  const GDPROverview = (await gdprOverviewPromise).default;
  const blueconicSource = (await gdprBlueconicSourcePromise).default;
  const getBusinessPartnerDataForCustomer = (await gdprGetBusinessPartnerDataSourcePromise).default;
  const gdprGetPartyAgreementsSource = (await gdprGetPartyAgreementsSourcePromise).default;
  const getActivityForCustomer = (await gdprGetActivityForCustomerSourcePromise).default;
  const gdprGetMarketingDataSource = (await gdprGetMarketingDataSourcePromise).default;

  return new GDPROverview(element, templates)
    .addSources(getBusinessPartnerDataForCustomer())
    .addSources(gdprGetPartyAgreementsSource())
    .addSources(getActivityForCustomer())
    .addSources(gdprGetMarketingDataSource())
    .addSources(blueconicSource(window.app.blueconic.Connector))
    .init();
});
